import React, { FC } from 'react'
import styled from 'styled-components';
import { IconButton, makeStyles } from '@material-ui/core';
import Back from '@material-ui/icons/ArrowBackIos';
import { withRouter } from 'react-router-dom';

type Props = {
    title: string, 
    history: any, 
    match: any, 
    location: any,     
}

const StyledSpan = styled.span`
    color: #FFF;
    line-height: 43px;
    font-family: "Roboto";
`

const Container = styled.div`
    background: rgb(2, 49, 93);
    height: 43px;
    font-family: "Roboto";
    font-size: 18px;
    text-align: center;
    box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.35);
    margin: 0 0 13px 0;
`
const useStyles = makeStyles(theme => ({
    button: {
        margin: theme.spacing(0),
        top: 0,
        left: 0,
        color: 'white',
        position: 'absolute'
    },
  }));
  
const onBack = (history: any) => () => {
    history.goBack()
}

const Heading : FC<Props> = ({title, history}) => {
    const classes = useStyles();
    return (
        <Container>
            <IconButton className={classes.button} onClick={onBack(history)} aria-label="back">
                <Back />
            </IconButton>            
            <StyledSpan>{title}</StyledSpan>
        </Container>
    )
}

export default withRouter(Heading)
