import React, { FC } from 'react'
import styled from 'styled-components';
import Row from './Row';


const Container = styled.div`
    font-family: "Roboto";
    font-size: 12px;
    padding: 0 18px 0 18px;
    width: 100%;
`

type RenderRow = (item: any) => JSX.Element

type Props = {
    items: any[],
    renderRow: RenderRow
}

const ListItemsRender = (items: any[], renderRow: RenderRow) => items.map((i, index) => {
    return (<Row key={index}>{renderRow(i)}</Row>) 
})

const List: FC<Props>   = ({items, renderRow} : Props) => {
    return (
        <Container>
            {ListItemsRender(items, renderRow)}
        </Container>
    )
}

export default List
