import { useEffect, useState, useContext } from 'react'
import axios from 'axios'
import { TransformFunction } from '../types/TransformFunction';
import UserContext from '../contexts/UserContext';
import urls from '../helpers/urls';

const useLoadData = (url: string, transform: TransformFunction) => {
    const [items, setItems] = useState([])
    const [wait, setWait] = useState(true)
    const [error, setError] = useState<string>()

    const user = useContext(UserContext)
    
    useEffect(() => {
        setWait(true)
        axios.get(urls.generateUserUrl(url, user))
        .then(resp => {
            setItems(transform(resp))
            setWait(false)
        })
        .catch(reason => {
            console.log("Get catch error. Reason", reason)
            setItems([])
            setError('Se produjo un error en el servidor')
            setWait(false)
        })
    }, [url, transform, user])    

    return { items, wait, error }
}

export default useLoadData