import productive from "../productive";

const url_productive = 'http://appsalud.gestionmsi.gob.ar'
const url_develop = 'http://localhost:3002'

const url_base = productive ? url_productive : url_develop


const internaciones = `${url_base}/api/Mobile/ObtenerInternaciones`
const turnos = `${url_base}/api/Mobile/ObtenerTurnos`
const turnos_reservados = `${url_base}/api/Mobile/ObtenerTurnosReservados`
const datos_paciente = `${url_base}/api/Mobile/ObtenerDatosPaciente`
const info_laboratorio = `${url_base}/api/Mobile/InfoLaboratorio`
const resumen = `${url_base}/api/Mobile/ObtenerResumenCronologico`

// POSTs (fromBody SignUpInfo)
const signIn = `${url_base}/frontend/token`
const signUp = `${url_base}/Registracion`
const reSendMail = `${url_base}/ReenviarEmailDeActivacion`
const forgotKey = `${url_base}/OlvideMiClave`

const sendLaboratoryData = `${url_base}/EnviarPorEmailDatosDeLaboratorio`
const changeEmail = `${url_base}/CambiarEmail`


type UserBasic = {
    dni: string, 
    selectedDni?: string,
}
const errorMsg = "Usuario no registrado"

const generateUserUrl = (urlbase: string, user: UserBasic | null) => {
    if (user == null)
        throw new Error(errorMsg);
    const dni = user.selectedDni ? user.selectedDni : user.dni

    const url = productive ? `${urlbase}/${dni}` : urlbase
    console.log("Generated Url", url)
    return url
}

const urls = {
    internaciones,
    turnos,
    turnos_reservados,
    datos_paciente,
    info_laboratorio,
    resumen,
    signIn,
    signUp,
    reSendMail,
    forgotKey,
    generateUserUrl,
    sendLaboratoryData,
    changeEmail,
}

export default urls;