import React, { FC, useEffect } from 'react'
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
import logo from './../../assets/logo.png'
import { login } from './../../helpers/routes'

const Container = styled.div`
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    position: absolute;
    box-sizing: border-box;
    background: rgb(1, 50, 93);
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    overflow: hidden;
`
const LogoContainer = styled.div`
    height: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    
    img {
        height: 100%;
    }
`

const StyledSpan = styled.span`
    color: #FFF;
    height: 38px;
    font-family: "Roboto";
    font-size: 15px;
    line-height: 38px;  
    position: fixed;
    bottom: 5px;
    right: 0;
    margin: 16px;  
`

type Props = {
    version: string, 
    history: any, 
    match: any, 
    location: any, 
}

const SplashPage :FC<Props> = ({ version, history }) => {
    useEffect(() => {
        setTimeout(() => {
            console.log("Ir a", login)
            history.push(login)
        }, 2000)        
    }, [history])
    
    return (
        <Container className="soft-tran">
            <LogoContainer><img src={logo} alt="logo"/></LogoContainer>
            <StyledSpan>{version}</StyledSpan>
        </Container>
    )
}

SplashPage.defaultProps = {
    version: '1.0.0'
}

export default withRouter(SplashPage)
