import React from 'react'
import MenuItem from './base/MenuItem';
import styled from 'styled-components';


const Container = styled.div`
    top: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-direction: row;     
    flex-wrap: wrap;  
`

/* 
Datos del paciente | Turnos (anteriores | reservados) | Internaciones | Diag. por Imágenes | Resultados Lab | 
*/
type Props = {
    onSelection: (option: string) => void, 
    menuItems: {id: string, title: string, icon: React.ReactElement }[]
}

const MainMenu = ({ onSelection, menuItems }: Props) => {
    return (
        <Container>
            {
                menuItems.map(mi => <MenuItem key={mi.id} {...mi} onSelection={onSelection} />)
            }
        </Container>
    )
}

export default MainMenu
