import React from 'react';
import { MemoryRouter as Router, Route, Switch } from "react-router-dom";
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { setUserFunctions } from './types/setUserFunction';
import LoginPage from './components/pages/LoginPage'
import SignupPage from './components/pages/SignupPage'
import SplashPage from './components/pages/SplashPage'
import PasswordRecoveryPage from './components/pages/PasswordRecoveryPage'

import {
  login,
  signup,
  password_recovery
} from './helpers/routes';

const version = "1.0.1"

type Props = {
    setUser: setUserFunctions
}

const routes = (location: any, setUser: setUserFunctions) => {
  return (
    <TransitionGroup>
      <CSSTransition
                key={location.key}
                classNames="soft-tran"
                timeout={500}
      >
        <Switch location={location}>
          <Route path="/" exact component={SplashPage} />
          <Route path={`/${login}`} exact render={() => <LoginPage setUser={setUser} />} />
          <Route path={`/${signup}`} exact render={() => <SignupPage setUser={setUser} />} />
          <Route path={`/${password_recovery}`} exact component={PasswordRecoveryPage} />
          <Route render={() => (<SplashPage version={version} />) } />
        </Switch>
      </CSSTransition>
  </TransitionGroup>       
  )
}

function AppNotLogUser({ setUser } : Props) {
  return (
    <Router>
      <Route render={({location}) => routes(location, setUser)} />
    </Router>
  );
}

export default AppNotLogUser;
