import React from 'react'
import AppFrame from '../base/AppFrame';
import MainMenu from '../MainMenu';
import AccessAlarmIcon from '@material-ui/icons/AccessAlarm';
import {
    historical_booking,
    booking,
} from './../../helpers/routes';

const menuItems = [
    { id: historical_booking, title: "Turnos Anteriores", icon: <AccessAlarmIcon /> },
    { id: booking, title: "Turnos Reservados", icon: <AccessAlarmIcon /> },
]

const BookingSelectorPage = () => {
    const onSelectionFunc = (id: string) => {
        console.log(id)
    }
    
    return (
        <>
            <AppFrame title={"Turnos"} >
                <MainMenu menuItems={menuItems} onSelection={onSelectionFunc}></MainMenu>
            </AppFrame>
        </>
    )
}

export default BookingSelectorPage