import React from 'react'
import Cell from './base/Cell';
import styled from 'styled-components';
import CustomButton from './base/CustomButton';

const Line = styled.hr`
    height:1px; 
    border:none; 
    color: rgb(66, 66, 66, 0.5); 
    background-color: rgb(66, 66, 66, 0.5);
    margin-top: 5px;
`
const Container = styled.div`
    background-color: #E9E9E9;
    padding: 5px 15px 5px 15px;
`
/*
    Protocolo | Fecha | Práctica > Descripcion | Resultado | Valor normal | Tipo | Componente | Estado

*/

export type LaboratoryDetails = {
    service: string,
    description: string,
    result: string,
    normalValue: string,
    type: string,
    component: string,
    status: string    
}

type Props = {
    protocol: string,
    date: string,
    details: LaboratoryDetails[],
    send: (protocol: string) => () => void,
}

const getLaboratoryDetails = (details: LaboratoryDetails[]) => (
    details.map((d, i) => getLaboratoryDetail(d, i))
)

const getLaboratoryDetail = ({
    service,
    description,
    result,
    normalValue,
    type,
    component,
    status    
} : LaboratoryDetails,
    index: number) => {
    return (
        <Container key={index}>
            <Cell label="Práctica" data={description}></Cell>
            <Cell label="Resultado" data={result}></Cell>
            <Cell label="Valor normal" data={normalValue}></Cell>
            <Cell label="Unidad" data={type}></Cell>
            <Cell label="Componente" data={component}></Cell>
            <Cell label="Estado" data={status}></Cell>
            <Line />
        </Container>
    )
}

// Lo saco porque la información parece repetida <Cell label="" data={service}></Cell>

const Laboratory = ({
    protocol,
    date,
    details,
    send 
} : Props) => {
    return (
        <div>
            <Cell isTitle isDateTime label="Fecha" data={date}></Cell>
            <Cell isTitle label="Protocolo" data={protocol}></Cell>
            {
                getLaboratoryDetails(details)
            }
            <CustomButton
                onClick={send(protocol)}
                text="ENVIAR MI INFORMACIÓN" />              
        </div>
    )
}

export default Laboratory
