import React, { FC, useState } from 'react'
import Cell from './base/Cell';
import { UserType } from '../types/UserType';
import styled from 'styled-components';
import CustomButton from './base/CustomButton';
import ChangeEmail from './ChangeEmail';
import Axios from 'axios';
import urls from '../helpers/urls';

const Container = styled.div`
    font-family: "Roboto";
    font-size: 12px;
    padding: 20px;
    box-sizing: border-box;
    width: 100vw;
`

const PersonalData : FC<UserType> = (user: UserType) => {
    const [open, setOpen] = useState(false);
    const [email, setEmail] = useState("")

    const handleClose = () =>  {
      setOpen(false);
    }

    const handleAccept = () => {
        const data = {
            dni: user.dni,
            email: email, // No tengo información de estructura de este dato
        }

        Axios.post(urls.changeEmail, data)
        .then(() => {

        })
        .catch(() => {

        })
        .finally(() => {
            setOpen(false);
        })
        
    }  
    const onChangeMail = () => {
        setOpen(true);
    }

    return (
        <Container>
            <Cell isTitle label="" data={user.name}></Cell>
            <Cell label="DNI" data={user.dni}></Cell>
            <Cell isDateTime label="Fecha nacimiento" data={user.birthday}></Cell>
            <Cell label="Sexo" data={user.sex}></Cell>
            <Cell label="Nacionalidad" data={user.nationality}></Cell>
            <Cell label="País" data={user.country}></Cell>
            <Cell label="Provincia" data={user.state}></Cell>
            <Cell label="Partido" data={user.district}></Cell>
            <Cell label="Localidad" data={user.locality}></Cell>
            <Cell label="Domicilio" data={user.address}></Cell>
            <Cell label="C/P" data={user.postalCode}></Cell>
            <Cell label="Profesión" data={user.profession}></Cell>
            <Cell label="Nivel" data={user.level}></Cell>
            <Cell label="Obra Social" data={user.healthService}></Cell>
            <Cell label="Nro Afiliado" data={user.customerNumber}></Cell>
            <Cell label="Teléfono" data={user.phone}></Cell>
            <Cell label="Móvil" data={user.cellphone}></Cell>
            <Cell label="Emergencia" data={user.emergency}></Cell>
            <CustomButton text="Modificar mi mail registrado" onClick={onChangeMail} />
            <ChangeEmail 
                handleAccept={handleAccept}
                handleClose={handleClose} 
                setEmail={setEmail}
                open={open}/>
        </Container>
    )
}

export default PersonalData
