import axios from 'axios'
import urls from './urls';
import { UserType, RelativeType } from '../types/UserType';
import { SignUpType } from '../types/SignUpInfo';
import trimDocument from './trimDocument';

type LoginType = {
    dni: string, 
    password: string,
}

const getDocument = (user: any, relative: any): any => {
    return user.pers_NumeroDocumento !== relative.padrePersona.pers_NumeroDocumento ? 
    relative.padrePersona.pers_NumeroDocumento : 
    relative.padreFamiliar.pers_NumeroDocumento
}

const getRelatives = (user: any, familiares: any): RelativeType[] | undefined => {

    const relatives = familiares.map((f: any) => ({
        dni: trimDocument(getDocument(user, f)),
        name: f.nombreApellidoFamiliar,
    }))
    const dniTrimmed = trimDocument(user.pers_NumeroDocumento)
    const u = {dni: dniTrimmed, name: user.paciente};
    const relativesWithUser = [u, ...relatives]
    console.log(relativesWithUser)
    
    return relativesWithUser;
}

const tFunction = (o : any) : UserType => {
    return ({
        name: o.paciente,
        dni: trimDocument(o.pers_NumeroDocumento), 
        birthday: o.fechaNacimiento,
        sex: o.sexo, 
        nationality: o.nacionalidad,
        country: o.pais, 
        state: o.provincia,
        district: o.partido, 
        locality: o.localidad, 
        address: o.domicilio,
        postalCode: o.codigoPostal, 
        profession: o.profesion, 
        level: o.nivelEducacion, 
        healthService: o.obraSocial, 
        customerNumber: o.codigoHC, 
        phone: o.telefono,
        cellphone: o.telefonoCelular, 
        emergency: o.telefonoEmergencia, 
        relatives: getRelatives(o, o.familiares),
    })
}

const obtainUserData = (user: { dni: string}, token: any, resolve: any, reject: any) => {
        // Obtengo los datos de usuario y familiares

        const url = urls.datos_paciente
        console.log(url)

        axios.get(url, {})
        .then(response => {
            const user = tFunction(response.data)
            const userT = {...user, token, selectedDni: user.dni};
            console.log("USER", userT)
            resolve(userT)
        })
        .catch(() => {
            reject(null)
        })
    
}

// data: { access_token, expires_in }}
const signInAction = (user: LoginType) => (new Promise((resolve, reject) => {
    const signInData = { username: user.dni, password: user.password }
    const params = new URLSearchParams()
    params.append('username', user.dni);
    params.append('password', user.password);

    console.log(urls.signIn)
    console.log(signInData)

    axios.post(urls.signIn, params)
    .then(({data}) => {
        const token = data.access_token
        console.log("TOKEN", token)
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;

        obtainUserData(user, token, resolve, reject)
    }) 
    .catch(reason => {
        console.log('signInAction', reason)
        reject(reason)
    })
}))

const signUpAction = (user: SignUpType) => (new Promise((resolve, reject) => {
    axios.post(urls.signUp, user)
    .then((data) => {
        console.log(data)
        signInAction(user)
            .then(resolve)
            .catch(reject)
    })
    .catch(reason => reject(reason))
}))

export {
    signInAction,
    signUpAction,
}
