import React, { useContext } from 'react'
import AppFrame from '../base/AppFrame';
import MainMenu from '../MainMenu';
import BookMedicalSolid from './../../assets/BookMedicalSolid.svg'
import FlaskSolid from './../../assets/FlaskSolid.svg'
import HospitalRegular from './../../assets/HospitalRegular.svg'
import UserInjuredSolid from './../../assets/UserInjuredSolid.svg'
import UserSolid from './../../assets/UserSolid.svg'
import XRaySolid from './../../assets/XRaySolid.svg'

import {
    booking_selector,
    hospitalization,
    image_diagnosis,
    personal_data,
    laboratory,
    summary,
} from './../../helpers/routes';
import UserSelector from '../UserSelector';
import UserContext from '../../contexts/UserContext';
// https://fontawesome.com/icons
// 
const menuItems = [
    { id: personal_data, title: "Datos del paciente", icon: (<img src={UserSolid} alt=""/>) },
    { id: booking_selector, title: "Turnos", icon: <img src={BookMedicalSolid} alt=""/> }, 
    { id: hospitalization, title: "Internaciones", icon: (<img src={HospitalRegular} alt=""/>) }, 
    { id: image_diagnosis, title: "Diag. por Imágenes", icon: <img src={XRaySolid} alt="" /> },
    { id: laboratory, title: "Resultados Lab", icon: <img src={FlaskSolid} alt=""/> },
    { id: summary, title: "Resumen", icon: <img src={UserInjuredSolid} alt=""/>}
]

const MainMenuPage = () => {
    const user = useContext(UserContext)
    
    const onSelectionFunc = (funcion: string) => {
        console.log(funcion)
    }
    
    return (
        <>
            <AppFrame title={"Mi información"}>
                {
                    user &&
                    user.selectedDni &&
                    <UserSelector user={user} selectedDni={user.selectedDni} />
                }
                <MainMenu menuItems={menuItems} onSelection={onSelectionFunc}></MainMenu>
            </AppFrame>
        </>
    )
}

export default MainMenuPage
