import React from 'react';
import { MemoryRouter as Router, Route, Switch } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import BookingPage from './components/pages/BookingPage'
import HistoricalBookingPage from './components/pages/HistoricalBookingPage'
import BookingSelectorPage from './components/pages/BookingSelectorPage'
import HospitalizationPage from './components/pages/HospitalizationPage'
import ImageDiagnosisPage from './components/pages/ImageDiagnosisPage'
import LaboratoryPage from './components/pages/LaboratoryPage'
import MainMenuPage from './components/pages/MainMenuPage'
import PersonalDataPage from './components/pages/PersonalDataPage'
import SummaryPage from './components/pages/SummaryPage'

import {
  historical_booking,
  booking,
  booking_selector,
  hospitalization,
  image_diagnosis,
  personal_data,
  laboratory,
  summary,
  main_menu,
} from './helpers/routes';


type Props = {
  logout: () => void // Por el momento no va a tener capacidad de logout
}

const routes = (location : any, isBack: boolean) => {
  console.log("isBack", isBack)
  //const back = isBack ? "my-node-back" : "my-node"
  return (
    <TransitionGroup>
      <CSSTransition
                key={location.key}
                classNames={"my-node"}
                timeout={500}
      >
        <Switch location={location}>
          <Route path={`/${main_menu}`} exact render={() => <MainMenuPage />} />
          <Route path={`/${historical_booking}`} exact component={HistoricalBookingPage} />
          <Route path={`/${booking}`} exact component={BookingPage} />
          <Route path={`/${booking_selector}`} exact component={BookingSelectorPage} />        
          <Route path={`/${hospitalization}`} exact component={HospitalizationPage} />
          <Route path={`/${image_diagnosis}`} exact component={ImageDiagnosisPage} />
          <Route path={`/${personal_data}`} exact component={PersonalDataPage} />
          <Route path={`/${laboratory}`} exact component={LaboratoryPage} />
          <Route path={`/${summary}`} exact component={SummaryPage} /> 
          <Route render={() => <MainMenuPage />} />
        </Switch>
      </CSSTransition>
    </TransitionGroup>
  )
}

function AppLogUser({ logout } : Props) {
  return (
      <Router>
        <Route render={(routing) => {
          console.log(routing)
          return routes(routing.location, 
            routing && routing.history && routing.history.action === "POP" ? true : false)}} />
      </Router>
  );
}

export default AppLogUser;
