import React, { FC } from 'react'
import styled from 'styled-components';

const StyledSpan = styled.span`
    color: #111;
    font-size: 16px;
    font-weight: bold;
    font-family: "Roboto";
`

const Title: FC<any> = ({children}) => {
    return (
        <StyledSpan>
            {children}
        </StyledSpan>
    )
}

export default Title
