import React, { FC, useState } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import styled from 'styled-components';
import { Link as LinkRouter } from "react-router-dom";

import {
  password_recovery,
  signup,
  main_menu,  
} from './../helpers/routes';
import { withRouter } from 'react-router-dom';
import { setUserFunctions } from '../types/setUserFunction';
import CustomButton from './base/CustomButton';
import { signInAction } from '../helpers/actions';
import Alert from './base/Alert';
import ProgressIndicator from './base/ProgressIndicator';

const Welcome = styled.span`
    color: rgb(2, 49, 93);
    font-weight: 700;
    font-size: 16px;
    line-height: 38px;
`
const AppDescription = styled.span`
    color: #888;
    font-weight: 300;
`

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

type Props = {
  history: any, 
  match: any, 
  location: any, 
  setUser: setUserFunctions,
}

const Login: FC<Props> = ({ history, setUser }) => {
  const [dni, setDni] = useState("16058176")
  const [password, setPass] = useState("mauro")
  const [error, setError] = useState()
  const [close, setClose] = useState(true)
  const [waiting, setWaiting] = useState(false)

  const logInSession = () => {
    setWaiting(true)
    console.log("Dni / Pass", dni, password)
    signInAction({ dni, password })
    .then(
      (user: any) => {
        setWaiting(false)
        setUser(user)
        history.replace(`/${main_menu}`)
      
      }
    )
    .catch(reason => {
      console.log("error en login")
      setWaiting(false)
      setError("Existe un error, revise los datos")
      setClose(false)
    })
    .finally(() => {

    })
  }
  const classes = useStyles();

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Welcome>¡BIENVENIDOS A SI SALUD!</Welcome>
        <AppDescription>La App del sistema de salud de San Isidro</AppDescription>
        <form className={classes.form} noValidate onSubmit={e => { e.preventDefault() }}>
          <TextField
            margin="normal"
            required
            fullWidth
            disabled={waiting}
            id="dni"
            label="Dni"
            name="dni"
            autoComplete="dni"
            autoFocus
            onChange={e => setDni(e.currentTarget.value)}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            disabled={waiting}            
            name="password"
            label="Contraseña"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={e => setPass(e.currentTarget.value)}
          />
          <CustomButton
            onClick={logInSession}
            text="INICIAR SESIÓN" />
          <Grid container>
            <Grid item xs>
              <LinkRouter to={password_recovery}>
                  ¿Olvidaste la contraseña?
              </LinkRouter>
            </Grid>
            <Grid item>
              <LinkRouter to={signup}>
                  Registrarme
              </LinkRouter>
            </Grid>
          </Grid>
        </form>
      </div>
      {
        !close && 
        <Alert 
            open={!close} 
            handleClose={() => setClose(true)} 
            message={error} />
      }  
      {
        waiting && <ProgressIndicator /> 
      }    
    </Container>
  );
}

export default withRouter(Login)