import React from 'react'
import {MedicalCare} from '../types/MedicalCare';
import Cell from './base/Cell';

/*
    Turnos reservados
    Hospital - Profesional - Especialidad - Tipo | Cancelable - Fecha hora

*/

type Props = {
    info: MedicalCare,
    dateTime: string, 
}

const Booking = ({info, dateTime} : Props) => {
    return (
        <div>
            <Cell label="Hospital" data={info.hospital}></Cell>
            <Cell label="Profesional" data={info.professional}></Cell>
            <Cell label="Especialidad" data={info.speciality}></Cell>   
            <Cell label="Tipo" data={info.type}></Cell>        
            <Cell label="Cancelable" data={info.state}></Cell>     
            <Cell label="Fecha hora" data={dateTime}></Cell>     
        </div>
    )
}

export default Booking
