import React, { FC } from 'react'
import Cell from './base/Cell'

/*
Resumen Cronológico

Tipo | Fecha | Detalle | Profesional | Estado

*/
type Props = {
    type: string,
    date: string,
    details: string,
    professional: string,
    status: string
}

const Summary : FC<Props> = ({type, date, details, professional, status} : Props) => {
    return (
        <div>
            <Cell isTitle isDateTime label="Fecha" data={date}></Cell>
            <Cell label="Tipo" data={type}></Cell>
            <Cell label="Detalle" data={details}></Cell>
            <Cell label="Profesional" data={professional}></Cell>
            <Cell label="Estado" data={status}></Cell>
        </div>
    )
}

export default Summary
