import React from 'react'
import Cell from './base/Cell';
import styled from 'styled-components';

const Container = styled.div`
    background-color: #EEE;
    padding: 5px 5px 5px 5px;
    margin: 2px;
`
/*
Practica | Cantidad | Fecha | Resultado
*/
type Props = {
    service: string,
    quantity: string,
    date: string,
    result: string
}

const HistoricalBookingDetails = ({service, quantity, date, result} : Props) => {
    return (
        <Container>
            <Cell label="Práctica" data={service} ></Cell>
            <Cell label="Cantidad" data={quantity}></Cell>
            <Cell isDateTime label="Fecha" data={date} ></Cell>
            <Cell label="Resultado" data={result}></Cell>
        </Container>
    )
}

export default HistoricalBookingDetails
