import React, { FC } from 'react'
import AppFrame from '../base/AppFrame';
import SignUp from '../SignUp';
import { setUserFunctions } from '../../types/setUserFunction';

type Props = {
    setUser: setUserFunctions
}

const SignupPage: FC<Props>  = ({setUser}) => {

    return (
        <>
            <AppFrame title={"Nueva cuenta"} >
                <SignUp setUser={setUser}></SignUp>
            </AppFrame>
        </>
    )
}

export default SignupPage
