import React, { FC } from 'react'
import styled from 'styled-components'

const Container = styled.div`
    padding-top: 5px;
`

const Line = styled.hr`
    height:1px; 
    border:none; 
    color: rgb(2, 49, 93, 0.5, 0.5); 
    background-color: rgb(2, 49, 93, 0.5);
    margin-top: 15px;
`

const Row : FC<any> = ({children}) => {
    return (
        <Container>
            {children}
            <Line />
        </Container>
    )
}

export default Row
