import React, { FC } from 'react'
import { Link } from "react-router-dom";
import styled from 'styled-components';

const Container = styled.div`
    font-family: "Roboto";
    font-size: 18px;
    background: rgb(12, 115, 194);
    height: 160px;
    width: 160px;
    max-height: 38vw;
    max-width: 38vw;
    margin: 5px;
    display: inline-block;
    border: 0px solid #000000;
    border-radius: 10px 10px 10px 10px;
    text-align: center;
    position: relative;
    clear: none;
    overflow: hidden;
    padding: 5px;
    box-sizing: border-box;
    a {
        color: white;
        font-weight: 500;
        text-decoration:none;
        font-family: "Roboto";        
        margin: 0;
        width: 100%;
        height: 100%;
    }

    @media screen and (max-device-width: 320px) {
        font-size: 15px;
    }

    :hover {
        transition: 300ms;
        background: rgb(1, 50, 93);
        box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.35);
    }
`

const IconContainer = styled.div`
    width: 100%;
    height: 66%;
    padding: 10px;
    box-sizing: border-box;
    color: white;
    svg, span, img, .svg-inline--fa {
        color: white;
        width: 100%;
        height: 100%; 
    }
`

const Span = styled.span`
    display: inline-block;
    height: 30px;

`

type Props = {
    id: string,
    title: string, 
    icon: React.ReactElement,
    onSelection: (option: string) => void
}

const MenuItem : FC<Props> = ({id, title, icon, onSelection} : Props) => {
    return (
        <Container onClick={() => onSelection(id)}>
            <Link to={`/${id}`}>
                <IconContainer>{icon}</IconContainer>
                <Span>{title}</Span>
            </Link>
        </Container>
    )
}

export default MenuItem