import React, {FC, useState} from 'react'
import styled from 'styled-components';
import Heading from './Heading'
import {CircularProgress} from '@material-ui/core';
import Alert from './Alert';

/*
background: rgb: 1, 50, 93

line 1 screen: rbg: 125, 150, 181

icon: rgb: 12, 115, 194

header text: 109, 109, 109
*/

const AppFrameContainer = styled.div `
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    position: absolute;
    box-sizing: border-box;
    font-family: "Roboto";
`

const ExterContainer = styled.div `
    width: 100%;
    height: calc(100% - 53px);   
    overflow: auto;
    top: 0;
    position: relative;
    box-sizing: border-box;
`

const InnerContainer = styled.div `
    margin-top: 0;
    padding: 0 15px 0 15px;
    
    position: relative;
    box-sizing: border-box;
    overflow: auto;

    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    flex-direction: column;    
`

type Props = {
    children?: any,
    title: string,
    waiting?: boolean,
    message?: string,
}

const AppFrame : FC <Props> = ({children, title, waiting, message} : Props) => {
    const [close, setClose] = useState(message ? false : true)
    
    return (
        <AppFrameContainer>
            <Heading title={title}/> 
            <ExterContainer className="my-node my-node-back soft-tran">
                <InnerContainer>
                {
                    waiting ? 
                    <CircularProgress/> : 
                    <>
                        {children}
                        {message && 
                            <Alert 
                                open={!close} 
                                handleClose={() => setClose(true)} 
                                message={message} /> }
                    </>
                }
                </InnerContainer>
            </ExterContainer>
        </AppFrameContainer>
    )
}
//             <div className="my-node" >
// <Container component="main" maxWidth="xs">
AppFrame.defaultProps = {
    waiting: false,
}

export default AppFrame
