import React, { ComponentProps } from 'react'
import AppFrameList from '../base/AppFrameList';
import Hospitalization from '../Hospitalization';
import { RenderFunction } from '../../types/RenderFunction'
import urls from '../../helpers/urls'
import useLoadData from '../../hooks/useLoadData';
import { TransformFunction } from '../../types/TransformFunction';

const render: RenderFunction = i => <Hospitalization key={i.internacionID} {...i} />;

type State = {
    items: ComponentProps<typeof Hospitalization>
}
/*
    hospital: string,
    professional: string,
    state: string,
    speciality: string,
    type: string

    info: MedicalCare,
    createReason: string,
    diagnosis: string,
    diagnosis2: string,
    hospitalizationDate: Date,
    endHospitalizationDate: Date
*/

// Transformación validada
const transform: TransformFunction = response => response.data.map(({
    hospital, 
    profesional,
    sector,
    tipoInternacion,
    motivoAlta, 
    diagnosticoPrincipal,
    diagnosticoSecundario,
    inicio_Internacion,
    fin_Internacion
} : any) => ({
    info: {
        hospital: hospital,
        professional: profesional,
        state: '-',
        speciality: sector,
        type: tipoInternacion,
    },
    createReason: motivoAlta,
    diagnosis: diagnosticoPrincipal,
    diagnosis2: diagnosticoSecundario,
    hospitalizationDate: inicio_Internacion,
    endHospitalizationDate: fin_Internacion,
}))

const HospitalizationPage = () => {
    const { items, wait, error } = useLoadData(urls.internaciones, transform)

    return (
        <>
            <AppFrameList 
                title="Internaciones" 
                items={items} 
                renderRow={render} 
                waiting={wait}
                message={error} />
        </>
    )
}

export default HospitalizationPage
