import React, { ComponentProps } from 'react'
import AppFrameList from '../base/AppFrameList';
import HistoricalBooking from '../HistoricalBooking';
import { RenderFunction } from '../../types/RenderFunction'
import urls from '../../helpers/urls'
import { TransformFunction } from '../../types/TransformFunction';
import useLoadData from '../../hooks/useLoadData';

const render: RenderFunction = i => <HistoricalBooking key={i.internacionID} {...i} />;

type State = {
    items: ComponentProps<typeof HistoricalBooking>
}

const transformDetails = (details: any) => details.map((d: any) => (
    {
        service: d.practica,
        quantity: d.cantidad,
        date: d.fecha,
        result: d.resultado,   
    }
))
// Transformación validada
const transform: TransformFunction = response => response.data.map(({
    hospital, 
    profesional,
    estadoTurno,
    especialidad,
    tipoTurno,
    fechaAtencion,
    fechaAdmision,
    tipoAdmision,
    detallev_TSI_TurnoConsumosPracticas,
} : any) => ({
    info: {
        hospital: hospital,
        professional: profesional,
        state: estadoTurno,
        speciality: especialidad,
        type: tipoTurno,
    },
    serviceDate: fechaAtencion,
    acceptanceDate: fechaAdmision,
    acceptanceType: tipoAdmision,
    details: transformDetails(detallev_TSI_TurnoConsumosPracticas),
}))


const HistoricalBookingPage = () => {
    const { items, wait, error } = useLoadData(urls.turnos, transform)    

    // HistoricalBookingDetails
    return (
        <>
            <AppFrameList 
                title="Turnos del Paciente" 
                items={items} 
                renderRow={render} 
                waiting={wait}
                message={error} />
        </>
    )
}

export default HistoricalBookingPage
