import React, { ComponentProps } from 'react'
import AppFrameList from '../base/AppFrameList';
import Summary from '../Summary';
import { RenderFunction } from './../../types/RenderFunction'
import urls from '../../helpers/urls';
import { TransformFunction } from '../../types/TransformFunction';
import useLoadData from '../../hooks/useLoadData';

const render: RenderFunction = i => <Summary {...i} />;

type State = {
    items: ComponentProps<typeof Summary>
}

/*
type, date, details, professional, status
*/

// Transformación validada
const transform: TransformFunction = response => response.data.map(({
    tipo,
    fecha,
    detalle,
    profesional,
    estado
} : any) => ({
    type: tipo, 
    date: fecha, 
    details: detalle, 
    professional: profesional, 
    status: estado,
}))

const SummaryPage = () => {
    const { items, wait, error } = useLoadData(urls.resumen, transform)

    return (
        <>
            <AppFrameList 
                title="Resumen cronológico" 
                items={items} 
                renderRow={render} 
                waiting={wait}
                message={error} />
        </>
    )
}

export default SummaryPage
