import React from 'react'
import moment from 'moment'
import Data from './Data';
import Label from './Label';
import styled from 'styled-components';
import Title from './Title';

const Container = styled.div.attrs((props: any) => ({
    ...props
})
)`
    font-size: 12px;
    padding: ${props => props.size};
    width: 100%;
    position: relative;
    background-color: ${props => props.color};
    box-sizing: border-box;
`
const LabelContainer = styled.div`
    width: 40%;
    display: inline-block;
`

const DataContainer = styled.div`
    width: 40%;
    display: inline-block;    
`
type Props = {
    data: string, 
    label: string, 
    isDetail?: boolean,
    isTitle?: boolean,
    isDateTime?: boolean,
}

const Cell = ({data, label, isDetail, isTitle, isDateTime}: Props) => {
    const padding = isDetail ? "15px 15px 15px 15px" : "7px 0px 3px 0px"
    const color = isDetail ? "#E9E9E9" : "#00"
    const dataF = isDateTime ? 
        moment(Date.parse(data)).format('D/M/YY H:mm') : 
        data
        // 'llll'
    return (
        <Container size={padding} color={color} >
            {
                isTitle ? 
                <>
                    <Title>{label} {dataF}</Title>
                </> :
                <>
                    <LabelContainer><Label>{label}</Label></LabelContainer>
                    <DataContainer><Data>{dataF}</Data></DataContainer>
                </>
            }
        </Container>
    )
}

export default Cell
