import React, { ComponentProps } from 'react'
import AppFrameList from '../base/AppFrameList';
import Booking from '../Booking';
import { RenderFunction } from '../../types/RenderFunction'
import urls from '../../helpers/urls'
import useLoadData from '../../hooks/useLoadData';
import { TransformFunction } from '../../types/TransformFunction';

const render: RenderFunction = i => <Booking key={i.internacionID} {...i} />;

type State = {
    items: ComponentProps<typeof Booking>
}

// Hospital, Profesional, Especialidad, tipo turno, cancelable, FechaHora
const transform: TransformFunction = response => response.data.map(({
    hospital, 
    profesional,
    cancelable,
    especialidad,
    tipoTurno,
    fechaHora,
} : any) => ({
    info: {
        hospital: hospital,
        professional: profesional,
        state: cancelable,
        speciality: especialidad,
        type: tipoTurno,
    },
    serviceDate: fechaHora,
    acceptanceDate: "",
    acceptanceType: ""
}));

const BookingPage = () => {
    const { items, wait, error } = useLoadData(urls.turnos_reservados, transform)

    return (
        <>
            <AppFrameList 
                title="Turnos reservados" 
                items={items} 
                renderRow={render} 
                waiting={wait} 
                message={error} />
        </>
    )
}

export default BookingPage
