import React from 'react'
import {MedicalCare} from '../types/MedicalCare'
import Cell from './base/Cell';
/*
Internaciones
Hospital - Profesional - Estado - Especialidad | Motivo Alta | Diagnóstico | Diagnóstico sec. | Inicio internación | Fecha Alta

*/
type Props = {
    info: MedicalCare,
    createReason: string,
    diagnosis: string,
    diagnosis2: string,
    hospitalizationDate: string,
    endHospitalizationDate: string
}

const Hospitalization = (props : Props) => {
    return (
        <div>
            <Cell isTitle label="" data={props.info.hospital}></Cell>
            <Cell label="Profesional" data={props.info.professional}></Cell>
            <Cell label="Especialidad" data={props.info.speciality}></Cell>
            <Cell label="Tipo de internación" data={props.info.type}></Cell>
            <Cell label="Motivo Alta" data={props.createReason}></Cell>
            <Cell label="Diagnóstico" data={props.diagnosis}></Cell>
            <Cell label="Diagnóstico Sec." data={props.diagnosis2}></Cell>
            <Cell isDateTime label="Inicio Inter." data={props.hospitalizationDate}></Cell>
            <Cell isDateTime label="Fecha Alta" data={props.endHospitalizationDate}></Cell>
        </div>
    )
}

export default Hospitalization
