import React, { FC } from 'react'
import styled from 'styled-components';
import Login from './../Login'
import { setUserFunctions } from '../../types/setUserFunction';
import logo from './../../assets/logo.png'


const BigHeading = styled.div`
    background: rgb(2, 49, 93);
    height: 138px;
    font-family: "Roboto";
    font-size: 15px;
    text-align: center;
    box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.35);
    margin: 0 0 15px 0;
`
const LogoContainer = styled.div`
    position: relative;
    top: 70px;
    height: 120px;
    width: 120px;
    margin: auto;
    background: rgb(2, 49, 93);
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center; 
    box-shadow: 0px 0px 40px 0px rgba(0,0,0,1);  
    animation-name: anime;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    border-color: rgb(2, 49, 93, 0.5);
    border-width: 2px;
    border-style: solid;
    img {
        padding: 20px;
        position: relative;
        width: 100%;
    }

    @keyframes anime {
        from {
            border-color: rgb(2, 49, 93, 0.2);
            box-shadow: 0px 0px 80px 0px rgba(0,0,0,1);  
        }
        to {
            border-color: rgb(2, 49, 93, 1);
            box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.3); 
        }
    }    
`

type Props = {
    setUser: setUserFunctions
}

const LoginPage : FC<Props> = ({setUser}) => {

    return (
        <div className="soft-tran">
            <BigHeading><LogoContainer><img src={logo} alt="logo"/></LogoContainer></BigHeading>
            <Login setUser={setUser}></Login>
        </div>
    )
}

export default LoginPage
