import React, { useState } from 'react'
import AppFrame from '../base/AppFrame';
import CustomButton from '../base/CustomButton';
import Label from '../base/Label';
import TextField from '@material-ui/core/TextField';
import Axios from 'axios';
import urls from '../../helpers/urls';
import Alert from '../base/Alert';
import ProgressIndicator from '../base/ProgressIndicator';

/*
  DNI 
  Email 
*/            
const PasswordRecoveryPage = () => {
    const [email, setEmail] = useState("")
    const [dni, setDNI] = useState("")
    const [error, setError] = useState()
    const [close, setClose] = useState(true)
    const [waiting, setWaiting] = useState(false)

    const directions = "Ingresá la dirección de email con la que te registraste y en breve te enviaremos" +
            " un correo para recuperar tu contraseña"

    const sendMail = () => {
      const data = {
        email,
        dni,
      }
      setWaiting(true)
      Axios.post(urls.forgotKey, data)
      .then(() => {
        setError("Se envió un mail a su casilla de correos")
      })
      .catch(reason => {
        setError("Existe un error, revise los datos")
        setClose(false)
      })
      .finally(() => {
        setWaiting(false)
      })      
    }
    
    return (
        <AppFrame title={"Recuperar contraseña"}>
          
          <Label>{directions}</Label>
            <TextField
              margin="normal"
              required
              fullWidth
              id="dni"
              label="DNI"
              name="dni"
              onChange={e => setDNI(e.currentTarget.value)}
              autoFocus/>          
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Dirección de email"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={e => setEmail(e.currentTarget.value)}
            />
          <CustomButton onClick={sendMail} text="ENVIAR" />
          {
              !close && 
              <Alert 
                  open={!close} 
                  handleClose={() => setClose(true)} 
                  message={error} />
          }      
          {
              waiting && <ProgressIndicator /> 
          } 
        </AppFrame>
    )
}

export default PasswordRecoveryPage