import React from 'react'
import { FormControl, InputLabel, Select } from '@material-ui/core';
import { UserType, RelativeType } from '../types/UserType';



type Props = {
    user: UserType,
    selectedDni: string,
}

export default function UserSelector({ user, selectedDni } : Props) {
    const handleChange = (dni: string) => {
        console.log("DNI", dni);
        user.selectDni && user.selectDni(dni)
    }
    
    const getOptions = (items: RelativeType[] | undefined) => (
        items && items.map((i: RelativeType) => <option key={i.dni} value={i.dni}>{i.name}</option>)
    )

    return (
        <FormControl>
            <InputLabel htmlFor="relatives-native">Familiares</InputLabel>
            <Select
                native
                value={selectedDni}
                onChange={(e: any) => handleChange(e.target.value)}
                inputProps={{
                    name: 'relatives',
                    id: 'relatives-native',
                }}
            >{getOptions(user.relatives)}</Select>
        </FormControl>
    )
}
