import React, { ComponentProps, useState } from 'react'
import {MedicalCare} from '../types/MedicalCare';
import Cell from './base/Cell';
import HistoricalBookingDetails from './HistoricalBookingDetails';
import CustomButton from './base/CustomButton';

/*
    Turnos del paciente
    Hospital - Profesional - Estado - Especialidad - Tipo | Fecha Atención | Fecha Admisión | Tipo admisión

*/

type Props = {
    info: MedicalCare,
    serviceDate: string,
    acceptanceDate: string,
    acceptanceType: string, 
    details: ComponentProps<typeof HistoricalBookingDetails>[],
}

const HistoricalBooking = ({info, serviceDate, acceptanceDate, acceptanceType, details} : Props) => {

    const [showDetails, setshowDetails] = useState(false)
    
    const getDetails = (details: ComponentProps<typeof HistoricalBookingDetails>[]) => (
        details.map(d => <HistoricalBookingDetails {...d} />)
    )
    const textButton = !showDetails ? "Ver prácticas" : "Ocultar prácticas"
    return (
        <div>
            <Cell isTitle isDateTime label="Fecha de atención" data={serviceDate}></Cell>     
            <Cell label="Hospital" data={info.hospital}></Cell>
            <Cell label="Profesional" data={info.professional}></Cell>
            <Cell label="Estado" data={info.state}></Cell>
            <Cell label="Especialidad" data={info.speciality}></Cell>   
            <Cell label="Tipo" data={info.type}></Cell>        
            <Cell isDateTime label="Fecha Admisión" data={acceptanceDate}></Cell>     
            <Cell label="Tipo Admisión" data={acceptanceType}></Cell>  
            {
                details.length && <CustomButton text={textButton} onClick={() => setshowDetails(!showDetails)} />
            }
            {
                showDetails && getDetails(details)
            }
        </div>
    )
}

export default HistoricalBooking
