import React, { useState } from 'react';
import AppLogUser from './AppLogUser';
import AppNotLogUser from './AppNotLogUser';
import UserContext from './contexts/UserContext';
import { UserType } from './types/UserType';

function App() {
  const [user, setUser] = useState<UserType | null>(null)
  
  const onSelectDni = (user: UserType ) => (dni: string) => {
    if (user) {
      onSetUser({...user, selectedDni: dni})
      console.log("onSelectDni", dni)
    }
  }
  
  const onSetUser = (userSelected: UserType | null) => {
    if (userSelected != null) {
      console.log("Set User")
      const _user = {
        ...userSelected,
        selectDni: onSelectDni(userSelected),
      }
      setUser(_user)
    }
  }
  return (
    <>
      <UserContext.Provider value={user}>
        {
          user ? 
          <AppLogUser logout={() => setUser(null)}></AppLogUser> :
          <AppNotLogUser setUser={(userSelected) => onSetUser(userSelected)}></AppNotLogUser>
        }
      </UserContext.Provider>
    </>
  );
}

export default App;
