import React, { FC } from 'react'
import AppFrame from './AppFrame'
import List from './List'
import Label from './Label';

type Props = {
    title: string,
    items: any[],
    renderRow: (item: any) => JSX.Element,
    waiting?: boolean    
    children?: React.ReactNode,
    message?: string    
}

const AppFrameList : FC<Props>  = ({title, items, renderRow, waiting, children, message} : Props) => {
    return (
        <AppFrame title={title} waiting={waiting} message={message}>
            <>
                {children}
                {
                    (items && items.length > 0) ? 
                    <List items={items} renderRow={renderRow}/> :
                    <Label>No hay información aún</Label>
                }
            </>
        </AppFrame>
    )
}

export default AppFrameList
