import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

type Props = {
    open: boolean,
    handleClose: () => void,
    handleAccept: () => void, 
    setEmail: (mail: string) => void,
}

export default function ChangeEmail({open, handleClose, handleAccept, setEmail}: Props) {

  return (
    <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Cambiar correo electrónico</DialogTitle>
        <DialogContent>
            <DialogContentText>
            Ingrese el nuevo correo electrónico donde llegarán las notificaciones, información y recuperación de clave. 
            Por favor, recuerde el correo seleccionado, ya que es muy importante
            </DialogContentText>
            <TextField
                autoFocus
                margin="dense"
                id="name"
                label="Correo electrónico"
                type="email"
                fullWidth
                onChange={e => setEmail(e.currentTarget.value)}            
            />
        </DialogContent>
        <DialogActions>
            <Button onClick={handleClose} color="primary">
            Cancelar
            </Button>
            <Button onClick={handleAccept} color="primary">
            Aceptar
            </Button>
        </DialogActions>
    </Dialog>
  );
}