import React, { ComponentProps} from 'react'
import AppFrameList from '../base/AppFrameList';
import { RenderFunction } from '../../types/RenderFunction'
import urls from '../../helpers/urls'
import Laboratory, { LaboratoryDetails } from '../Laboratory';
import useLoadData from '../../hooks/useLoadData';
import { TransformFunction } from '../../types/TransformFunction';

const render: RenderFunction = i => <Laboratory key={i.protocol} {...i} />;

type State = {
    items: ComponentProps<typeof Laboratory>
}

/*
    protocol,
    date,
    service,
    description,
    result,
    normalValue,
    type,
    component,
    status
*/

const toTree = (acc: any,
    {
        numeroProtocolo,
        fechaHora,
        practicacentral,
        practicadescripcion,
        resultado,
        valornormal,
        tipo,
        componente,
        status
    } : any       
) => {
    const accDetalles = (acc: any, numeroProtocolo: any) => {
        return acc[numeroProtocolo] ? acc[numeroProtocolo].detalles : []
    }
    return {
        ...acc,
        [numeroProtocolo]: {
            numeroProtocolo,
            fechaHora,
            detalles: [...accDetalles(acc, numeroProtocolo), {
                practicacentral,
                practicadescripcion,
                resultado,
                valornormal,
                tipo,
                componente,
                status
            }]        
        }
}}

const getLaboratoryDetails = (details: any) => 
    details.map((d: any) => getLaboratoryDetail(d))

const getLaboratoryDetail = ({
    practicacentral,
    practicadescripcion,
    resultado,
    valornormal,
    tipo,
    componente,
    status
}: any) => {
    const labDetails: LaboratoryDetails = {
        service: practicacentral,
        description: practicadescripcion,
        result: resultado,
        normalValue: valornormal,
        type: tipo,
        component: componente,
        status: status        
    }
    return labDetails
}
const send = (protocolo: string) => () => {
    console.log("Enviando información...");
}

const getLaboratory = ({
    numeroProtocolo,
    fechaHora,
    detalles,    
}: any) => {
    const lab : ComponentProps<typeof Laboratory> = {
        date: fechaHora,
        protocol: numeroProtocolo, 
        details: getLaboratoryDetails(detalles),
        send: send,
    }
    return lab
}

// Transformación validada
const transform: TransformFunction = response => {
    const obj = response.data.reduce(toTree, {})
    console.log(obj)
    return Object.keys(obj).map(k => getLaboratory(obj[k]))
}

const LaboratoryPage = () => {
    const { items, wait, error } = useLoadData(urls.info_laboratorio, transform)

    return (
        <>
            <AppFrameList 
                title="Laboratorio" 
                items={items} 
                renderRow={render} 
                waiting={wait}
                message={error}>
            </AppFrameList>
        </>
    )
}

export default LaboratoryPage
