import React, {FC, useState} from 'react';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import {makeStyles} from '@material-ui/core/styles';
import styled from 'styled-components';
import {main_menu} from './../helpers/routes';
import {withRouter} from 'react-router-dom';
import {setUserFunctions} from '../types/setUserFunction';
import CustomButton from './base/CustomButton';
import { signUpAction } from '../helpers/actions';
import Alert from './base/Alert';
import ProgressIndicator from './base/ProgressIndicator';

const Message = styled.span `
    color: rgb(2, 49, 93);
    font-weight: 700;
    font-size: 16px;
    line-height: 38px;
`

const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white
        }
    },
    paper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center'
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1)
    },
    submit: {
        margin: theme.spacing(3, 0, 2)
    }
}));

type Props = {
    history: any,
    match: any,
    location: any,
    setUser: setUserFunctions
}

const SignUp : FC < Props > = ({history, setUser}) => {
    const [idTarjeta, setIdTarjeta] = useState("")
    const [dni, setDNI] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [fechaNacimiento, setFechaNacimiento] = useState("")    
    const [error, setError] = useState()
    const [close, setClose] = useState(error ? false : true)
    const [waiting, setWaiting] = useState(false)

    const logInSession = () => {
        if (!idTarjeta || !dni || !email || !password || !fechaNacimiento)
            return 

        setWaiting(true)
        signUpAction({idTarjeta, dni, email, password, fechaNacimiento})
        .then(
          (user: any) => {
            history.replace(`/${main_menu}`)
            setUser(user)
           
          }
        )
        .catch(reason => {
          console.log("error en login")
          setError(reason.response.data)
          setClose(false)
        })
        .finally(() => {
            setWaiting(false)
        })        
    }
    const classes = useStyles();

    return (
        <div className={classes.paper}>
            <Message>Cargá tus datos personales</Message>
            <form className={classes.form} onSubmit={e => { e.preventDefault() }}>
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="idTarjeta"
                    label="Id Tarjeta"
                    name="idTarjeta"
                    onChange={e => setIdTarjeta(e.currentTarget.value)}
                    autoFocus />
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="dni"
                    label="DNI"
                    name="dni"
                    onChange={e => setDNI(e.currentTarget.value)}
                />
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    id="email"
                    label="E-mail"
                    name="email"
                    autoComplete="email"
                    type="email"
                    onChange={e => setEmail(e.currentTarget.value)}
                />
                <TextField
                    margin="normal"
                    required
                    fullWidth
                    name="password"
                    label="Contraseña"
                    type="password"
                    id="password"
                    onChange={e => setPassword(e.currentTarget.value)}
                />
                <TextField
                    margin="normal"
                    required
                    fullWidth                
                    id="date"
                    label="Fecha Nacimiento"
                    type="date"
                    defaultValue={Date()}
                    onChange={e => setFechaNacimiento(e.currentTarget.value)}
                    InputLabelProps={{
                    shrink: true,
                    }} />                   
                <FormControlLabel
                    control={< Checkbox value = "remember" color = "primary" />}
                    label="He leído los términos de uso y privacidad de esta app"/>
                <CustomButton onClick={logInSession} text="VALIDAR DATOS"/>
            </form>
            {
                !close && 
                <Alert 
                    open={!close} 
                    handleClose={() => setClose(true)} 
                    message={error} />
            }      
            {
                waiting && <ProgressIndicator /> 
            }                       
        </div>
    );
}

export default withRouter(SignUp)