import React, { FC } from 'react'
import styled from 'styled-components';
import { UserType } from '../types/UserType';

const Container = styled.div`
    margin: 0;
    padding-top: 30px;
    padding: 0;
    width: 95vw;
    height: 90vh;
    box-sizing: border-box;
    background: rgb(1, 50, 93);
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
`
type Props = {
    user: UserType,
}

const ImageDiagnosis : FC<Props> = ({user} : Props) => {
    const getUrl = (dni : String) => (`http://diagnostico.gestionmsi.gob.ar/default.aspx?user_name=string&password=sistemas&patient_id=${dni}`)
    return (
        <Container>
            {
                user.selectedDni && 
                <iframe
                    title="Imágenes"
                    id="frameContent"
                    width="100%"
                    height="100%"
                    style={{border: '0px', marginTop: '-100px'}}
                    src={getUrl(user.selectedDni)}>
                </iframe>
            }
        </Container>
    )
}
// style="border: 0px;"

export default ImageDiagnosis
